import React from "react";
import "./ConsultationCTASection.css";

interface ConsultationCTASectionProps {
  title?: string;
  description?: string;
  features?: string[];
  buttonText?: string;
  imageUrl?: string;
  onConsultationClick?: () => void;
}

const ConsultationCTASection: React.FC<ConsultationCTASectionProps> = ({
  title = "NHN T VN THIT K NI THT",
  description = "Bn ang mun thit k khng gian sng p v hin i? Hy lin h vi PG Design  c t vn min ph v nhn bo gi chi tit.",
  features = [
    "T vn thit k min ph",
    "Bo gi chi tit v minh bch",
    "i ng thit k chuyn nghip",
    "H tr thi cng v gim st"
  ],
  buttonText = "NG K T VN NGAY",
  imageUrl = "/src/assets/images/diary-image-1.png",
  onConsultationClick
}) => {
  const handleClick = () => {
    if (onConsultationClick) {
      onConsultationClick();
    } else {
      // Default behavior
      console.log("Consultation requested");
    }
  };

  return (
    <section className="consultation-cta">
      <div className="cta-container">
        <div className="cta-content">
          <h2 className="cta-title">
            {title}
          </h2>
          <p className="cta-description">
            {description}
          </p>
          <div className="cta-features">
            {features.map((feature, index) => (
              <div key={index} className="cta-feature">
                <span className="feature-icon"></span>
                <span className="feature-text">{feature}</span>
              </div>
            ))}
          </div>
          <button className="cta-button" onClick={handleClick}>
            {buttonText}
          </button>
        </div>
        <div className="cta-image">
          <img 
            src={imageUrl} 
            alt="Interior Design Consultation" 
            className="consultation-image"
          />
        </div>
      </div>
    </section>
  );
};

export default ConsultationCTASection; 